import React from 'react'

import './Header.css'

export default function Header() {
  return (
    <div className='header primary-dark-bg'>
      <header className='header-container'>
        <div className='header-logo'>
          <a className='' href='/'>
              <img src='http://robuxmarkets.com/assets/img/robuxmarket.png' alt='Logo' />
          </a>
        </div>
        <ul className='header-navigation'>
          <li>
            <a href='/'>Trang chủ</a>
          </li>

          <span className='v-separate'></span>

          <li>
            <a href='/history'>Lịch sử giao dịch</a>
          </li>

          <span className='v-separate'></span>

          <li>
            <a href='/auth/login'>
              <button buttontype="success">Đăng nhập</button>
            </a>
          </li>
          <li>
            <a href='/auth/register'>
              <button buttontype="primary">Đăng ký</button>
            </a>
          </li>
        </ul>
      </header>
    </div>
  )
}
