import React from 'react'
import changeTitle from '../../../utils/changeTitle'
import './Login.css'

import Input from '../../../components/Input'
import Button from '../../../components/Button'

export default function Login() {
  changeTitle('Đăng nhập')

  return (
    <div className='login-form-container'>
      <form className='primary-dark-bg'>
        <h2>Đăng nhập</h2>
        <Input placeHolder='Tên tài khoản' type='text' id='username'></Input>
        <Input placeHolder='Mật khẩu' type='password' id='password'></Input>

        <a className='login-link' href='/#'>Lấy lại mật khẩu</a>

        <Button buttontype="primary">ĐĂNG NHẬP</Button>

        <p>Bạn chưa có tài khoản? <a href='/auth/register'>Tạo tài khoản.</a></p>

        <p>- Hoặc -</p>

        <Button buttonType="primary" leftIcon={'❤ '}>Đăng nhập bằng Facebook</Button>

      </form>
    </div>
  )
}
