import React from 'react'
import RobloxIcon from '../../images/Roblox.svg'
import DiscordIcon from '../../images/Discord.svg'
import TwitterIcon from '../../images/Twitter.svg'

import './Footer.css'

export default function Footer() {
  return (
    <div className='footer primary-dark-bg'>
        <div className='footer-social-link'>
            <a className='social-container' href='/social'>
              <img src={RobloxIcon} alt="" />
            </a>
            <a className='social-container' href='/social'>
              <img src={DiscordIcon} alt="" />
            </a>
            <a className='social-container' href='/social'>
              <img src={TwitterIcon} alt="" />
            </a>
        </div>
        <span className='footer-copyright'>Copyright © robuxre.net 2022. All rights reserved.</span>
    </div>
  )
}
