import React from "react"

function Button({ buttonType = "primary", leftIcon, rightIcon, children, color }) {
    return (
        <button buttontype={buttonType} style={{ backgroundColor: color }}>
            {leftIcon && <span className='icon'>{leftIcon} </span>}
            <span>{children}</span>
            {rightIcon && <span className='icon'> {rightIcon}</span>}
        </button>
    )
}

export default Button