import React from 'react'
import changeTitle from '../../../utils/changeTitle'
import './Register.css'

import Input from '../../../components/Input'
import Button from '../../../components/Button'

export default function Register() {
  changeTitle('Đăng ký')

  return (
    <div className='register-form-container'>
      <form className='primary-dark-bg'>
        <h2>Đăng ký</h2>
        <Input placeHolder='Tên tài khoản' type='text' id='username'></Input>
        <Input placeHolder='Mật khẩu' type='password' id='password'></Input>
        <Input placeHolder='Nhập lại mật khẩu' type='password' id='password'></Input>

        <Button buttontype="primary">ĐĂNG KÝ</Button>

        <p>Bạn đã có tài khoản? <a href='/auth/login'>Đăng nhập.</a></p>

        <p>- Hoặc -</p>

        <Button buttonType="primary" color={'#1877F2'} leftIcon={'❤ '}>Đăng ký bằng Facebook</Button>

      </form>
    </div>
  )
}
